import { getAuth, signInWithEmailAndPassword, onAuthStateChanged } from "firebase/auth";

const auth = getAuth();

let user = null

function setUser(u) {
    user = u;
}

onAuthStateChanged(auth, (usr)=>{
    if (usr) {
        user = usr
        // setUser(usr)
    }
})


export {auth, signInWithEmailAndPassword, onAuthStateChanged, user, setUser}