import React, {useState, useEffect} from 'react'
import Banner from '../Components/banner/banner'
import Postbox from '../Components/posts/Postbox'
import {auth ,signInWithEmailAndPassword ,user as u} from '../firebase/auth'

export default function Admin() {

    const [loadingAuthData, setLoadingAuthData] = useState(true)
    const [user, setUser] = useState(null)

    useEffect(() => {
        if(u){
            setUser(u)
        }
        setLoadingAuthData(false)
    }, [user])
    
    //Log In.....
    function handleSubmit(event) {
        event.preventDefault();

        const form = document.getElementById('login');

        signInWithEmailAndPassword(auth, form.email.value, form.password.value)
        .then((userCredential) => {
            // Signed in 
            const user = userCredential.user;
            console.log(user)
            setUser(user)
        })
        .catch((error) => {
            const errorCode = error.code;
            const errorMessage = error.message;
            console.log(errorCode," ", errorMessage)
        });

    }


    //Rendering.....

    if (loadingAuthData) {
        return (
            <Banner title="Loading Auth data..."/>
        )
    } 
    
    else {
        if (user) {
            return (
                <>
                <Banner title= {`logged in as ${user.email}`} />
                <Postbox/>
                </>
            )
        }

        else {
            return (
                <>
                <Banner title="You need to sign in first..!"/>
                <form id='login' onSubmit={handleSubmit}>
                    <span>Enter email :</span>
                    <input type="email" name="email"></input><br/>
                    <span>Enter password :</span>
                    <input type="password" name="password"></input><br/>
                    <button type="submit">Submit</button>
                </form>
                </>
            )
        }
    }

}
