import {React, useEffect} from 'react'
import {user} from '../firebase/auth'
import Banner from '../Components/banner/banner'
import CommentSection from '../Components/commentsection/CommentSection'
import Crousel from '../Components/crousel/Crousel'
import EditInfo from '../Components/gameinfo/EditInfo'
import GameInfo from '../Components/gameinfo/GameInfo'

export default function Game(props) {
    useEffect(() => {
        document.title = `CDEK - ${props.name}`
     }, [props.name]);

    return (
        <>
        {/* <Navbar/> */}
        <Banner title={props.name}/>
        <Crousel name={props.name}/>
        { !user ? <GameInfo name={props.name} /> : <EditInfo name={props.name}/>}
        <CommentSection name={props.name}/>
        {/* <Footer/> */}
        </>
    )
}
