import React from 'react'
import './style.css'

export default function Comment(props) {
    return (
        <>
        <div className="comment">
            {props.isAdmin ? <h3 style={{color : 'brown'}}>{props.name}</h3> :<h3>{props.name}</h3>}
            <div className='timestamp'>{props.time}</div><hr/>
            {props.isAdmin ? <p style={{color : 'blue'}}>{props.comment}</p> :<p>{props.comment}</p>}
        </div>
        </>
    )
}
