import React, { useState, useEffect } from 'react'
import { db } from '../../firebase/config'
import { onSnapshot, collection, query, where } from "firebase/firestore";
import './style.css'

export default function GameInfo(props) {

    const [data, setData] = useState(null)
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        const q = query(collection(db, "games"), where('name', '==', props.name));

        onSnapshot(q, (querySnapshot) => {
            querySnapshot.forEach((doc) => {
                setData(doc.data());
            });
            setLoading(false);
        } )

    }, [props.name])

    let whatsNew = []

    if (data) {
        data.version.whatsNew.forEach(e => {
            whatsNew.push(<li>{e}</li>)
        })
    }

    if (loading) {
        return (
            <p>Loading data...</p>
        )
    }

    return (
        <>
        <br/>
        <div className="full-width">
            <div className="about">
                <p>{data.description}</p><br/>

                <h5>Made on : </h5>
                <p>{data.version.releasedOn}</p><br/>
                
                <h5>Last Update : </h5>
                <p>{data.version.lastUpdate}</p><br/>
                
                <h5>Version : </h5>
                <p>{data.version.version}</p><br/>
                
                <h5>Whats new : </h5>
                {whatsNew}

            </div>
        </div>
        <br/><br/><br/>

        <center>
            <center style={{width: '50%'}}>
                <a href={data.links.android}><div className="download-links">Download APK</div></a>
                <a href={data.links.windows}><div className="download-links">Download Windows</div></a>
                <a href={data.links.source}><div className="download-links">Source Code</div></a>
                <a href={`/playonline/${props.name}`}><div className="download-links">Play online</div></a>
            </center>
        </center>
        <br/>
        </>
    )
}
